#section-community {
  margin: 30px 0 0;
  text-align: center;

  // .main-section1
  //   // width: 1440px
  //   height: 645px

  .main-section-image {
    margin: 20px 0;
    max-width: 100%;
    width: 100%;
    // width: 1481.95px
 }    // height: 236.72px

  .main-section-bottom {
    margin: 20px auto 0;
    padding: 15px 15px 0;

    max-width: 800px;
    font-family: $font-family-heading;

    font-style: normal;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0.4000000059604645px;
    text-align: center;
    color: #fff;

    .main-section-bottom-bg {
      border-radius: 15px;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;

      padding: 30px;
      background: #F47F57;
      font-size: 18px;
      // background: linear-gradient(45deg, #000000 14.64%, #515151 85.36%)
      background: linear-gradient(45deg, #FF8B71 0%, #A81E41 100%);

      @media (max-width: 400px) {
        font-size: 14px;
        padding: 15px; } } }

  .f-text-title1 {
    font-family: $font-family-heading;
    font-style: normal;
    font-weight: bold;
    max-width: $max-width;
    font-size: 46px;
    line-height: 1.2;
    text-align: center;
    color: rgba(255, 255, 255, 1);
    padding: 0 15px;
    margin: 30px auto;

    @media (max-width: 400px) {
      font-size: 30px; } }

  .f-text-text {
    font-family: $font-family-body;
    font-size: 16px;
    max-width: 760px;
    line-height: 1.5;
    text-align: center;
    color: #A2A2A2;
    margin: 10px auto;
    padding: 0 15px;
    @media (max-width: 400px) {
      font-size: 14px; } } }
